.how-we-do__title {
  text-align: center;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  color: var(--primaryTextColor);
}
.how-we-do__list {
  --card-gap: 15px;
  @include mq (tab) {
    --card-gap: 20px;
    
  }
}

.how-we-do__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 20px;
  fill: var(--accent-color);
  box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);
  &:hover {
    fill: var(--bg-white-color);
    color: var(--bg-white-color);
    background-color: var(--accent-color);
  }
    @include mq (tab) {
      --card-item: 2;
    }
}

.how-we-do__icon {
  margin: 0 auto 20px auto;
}

.how-we-do__label {


  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;
  text-align: center;
  letter-spacing: 0.08em;
}

.how-we-do__label::after {
  content: "";
  display: block;
  margin: 20px auto ;
  width: 20px;
  height: 2px;

  background-color: var(--accent-color);

}

.how-we-do__item:hover .how-we-do__label::after {
  background-color: var(--bg-white-color);
}

.how-we-do__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  text-align: center;
  letter-spacing: 0.08em;
  color: var(--secondaryTextColor);
}

.how-we-do__item:hover .how-we-do__text {
 color: var(--bg-white-color);
}

